/* Reset some basic styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .navbar {
    display: flex;
    justify-content: center; /* Center the navbar items */
    align-items: center;
    background-color: #e77c7c;
    padding: 0;
    height: 60px;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    margin: 0;
    height: 100%;
  }
  
  .navbar-links li {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s ease;
    height: 100%;
    padding: 1rem;
  }
  
  .navbar-links a:hover {
    background-color: #f39e9e; /* Change background color on hover */
  }
  
  .navbar-links a:active {
    background-color: #f39e9e; /* Change background color on click */
  }
  